:root {
    --white: #ffffff;
    --pink: #fa8071;
    --darker-pink: #f46459;
}
.nav-tab {
    display: flex;
    background-color: var(--darker-pink);
    justify-content: space-between;
}

.nav-tab h1 {
    border: 0.2rem solid var(--white);
    border-radius: 1rem;
    padding: 8px 12px;
    margin: 8px;
    background-color: var(--darker-pink);
    color: var(--white);
    font-size: 18px;
}

nav {
    margin-right: 10px;
}

nav ul {
    display: flex;
    align-items: center;
    padding-right: 0;
    height: 100%;
}

nav ul li {
    display: inline;

}

nav a {
    color: var(--white);
    text-decoration: none;
    border: 0.2rem solid transparent;
    font-size: 14px;
    padding: 4px 2px;
}

nav a:hover {
    border: 0.2rem solid var(--white);
    border-radius: 1rem;
    font-size: 14px;
}

@media screen and (min-width: 426px) {

    nav ul {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        padding-right: 0;
        height: 100%;
        justify-content: space-around;
    }

    nav ul li {
        display: inline;

    }

    nav a {
        color: var(--white);
        padding: 0.5rem 1rem;
        margin: 1rem;
        text-decoration: none;
        border: 0.2rem solid transparent;
        font-size: 18px;
    }

    nav a:hover {
        border: 0.2rem solid var(--white);
        border-radius: 1rem;
        font-size: 18px;
    }

    #logo {
        border: 0.2rem solid var(--white);
        border-radius: 1rem;
        padding: 0.5rem 1rem;
        margin: 1rem;
        background-color: var(--darker-pink);
        font-size: 24px;
    }

    header {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        background-color: var(--darker-pink);
    }

    header h1 {
        padding-left: 50px;
        font-size: 2vw;
        background-color: var(--pink);
        color: var(--white);
    }

}